@-moz-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}
.portal-header{
  width: 100%;
  height: 120px;
  background: #FCCFA7;
  display: flex;
  padding: 0 103px;
  padding-right: 27px;
  justify-content: space-between;
  align-items: center;
  color: #FFFFFF;
  .down-up{
   display: flex;
   align-items: center;
   .network-select{
     height: 36px;
     .nw-wrapper{
       min-height: 36px;
       .nw-item{
         height: 36px;
       }
     }
   }
   .wallet-btn{
     height: 40px;
     border: 0;
   }
  }
  .link-btn{
    display: flex;
    align-items: center;
    margin-top: 21px;
    .mobile-arrow{
      display : none;
    }
    .pc-arrow {
      display : block;
    }
    img{
    cursor: pointer;
    }
    a{
      color: #FFFFFF;
      text-decoration: none;
    }
    .bit-it,.deri-io{
      width: 88px;
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #59AE99;
      background-repeat: no-repeat;
      margin-left: 20px;
    }
    .deri-io{
      background-color: #93A1B8;
    }
  }
}
.portal-header.collect{
  animation: headerOpen  0.5s;
}
.portal-header.collected{
  animation : headerClose 0.5s ; 
  height: 56px;
}
.collected{
  .link-btn{
    margin: 0;
  }
  .title-des{
    display: none;
  }
}

@keyframes headerClose{
  from {
    height : 120px;
  }
  to {
    height : 56px;
  }
}
@keyframes headerOpen{
  from {
    height : 56px;
  }
  to {
    height : 120px;
  }
}

@keyframes expand {
  from {
    width : 0px;
  }
  to {
    width : 100%;
  }
}
@keyframes shrink {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}

@media (0 < width < 761px) {
  .portal-header {
    display: none;
    width : 100vw;
    height: 100vh;
    position: fixed;
    z-index: 2;
    padding: 24px;
    .down-up {
      display : none;
    }
  }
  .portal-header.shrink {
    animation : shrink 0.5s;
    height: 100vh;
  }
  .portal-header.expand {
    display: flex;
    height: 100vh;
    animation: expand 0.5s;
    .title-link {
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-between;
      height: 100vh;
      align-items: flex-start;
      overflow : hidden;
      
      .link-btn {
        display : flex;
        flex-direction : column;
        margin-top: 20px;
        align-items: start;
        .mobile-arrow{
          display : block;
        }
        .pc-arrow {
          display : none;
        }
        .bit-it,.deri-io {
          background : transparent;
          justify-content: start;
          margin-top: 12px;
          margin-left: 0;
        }
      }
      .title-des{
        display: block;
        font-size: 14px;
        font-weight : 400;    
        margin: 0 0px 40px;
        white-space: nowrap;
      }
    }
  }
}